body {
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
}
body ul,
body ol,
body p,
body h1,
body h2,
body h3,
body h4,
body h5,
body h6 {
  margin: 0;
  padding: 0;
}
.movie-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}
.movie-card .movie-heading {
  margin-bottom: 20px;
}
.movie-card .movie-heading h1 {
  font-size: 22px;
}
.movie-card .movie-image {
  position: relative;
  width: 100%;
  padding-top: 100%;
}
.movie-card .movie-image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.movie-card .btn {
  margin-top: 20px;
}
